import { createUseStyles, useTheme, ThemeProvider } from 'react-jss';
import sizes from './Sizes'

const theme = {
    color: '#fafad2'
  }


export default createUseStyles( theme => ({
    MainPage: {
        boxSizing: 'border-box',
        display: 'flex',
        overflowY: 'visible',
        flexWrap: 'wrap',
        backgroundColor: theme.color,
        fontSize: '1.5rem',
        height: '700px',
        textIndent: '50px',
        padding: '0 25px',
        width: '100%',
        paddingTop: '20px',
        minHeight: '600px',
        [sizes.down('sm')]: {
    
            display: 'none',

            },
    },
    sp: {
        fontSize: '2rem'
    }
}))

